import React from "react";
//Tnne mys contet menu? Load playlist && Delete playlist tms.
//contextiin voisi tehd mm. save-as model
//tnne voisi laittaa sen create new jutunkin
//pit miettii uudelleen minne laittaa sen put to queue ja play playlist
//nekin voisi lyty siit context - menusta.
//&edit modalkin voisi olla siell context menussa linkkin?
//oikeet propsit vaa ni toimii.
//playlist-itemien eli niiden osittolistojen nimi tytyy parsia ett ei oo koko mitaltaan siell
//lis toolboxin kaikkiin niiihin, johon tulee sitten se nimi nkyviin.
import isEqual from "react-fast-compare";
import { ContextMenu, MenuItem, ContextMenuTrigger } from "react-contextmenu";
import "./contextmenu.css";
export default class Playlist extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: this.props.name.substring(0, 400),
    };
  }
  componentDidUpdate(prevProps) {
    if (!isEqual(this.props, prevProps)) {
      this.setState({
        name: this.props.name,
      });
    }
  }
  loadPlaylist(id) {
    this.props.loadPlaylist(id);
  }
  deletePlayList(id) {
    this.props.deletePlaylist(id);
  }
  render() {
    //console.log(this.props);
    return (
      <ContextMenuTrigger id={this.props._id}>
        <ContextMenu id={this.props._id}>
          <MenuItem
            data={{ foo: "bar" }}
            onClick={this.loadPlaylist.bind(this, this.props._id)}
          >
            Load playlist
          </MenuItem>
          <MenuItem
            data={{ foo: "bar" }}
            onClick={this.deletePlayList.bind(this, this.props._id)}
          >
            Delete playlist
          </MenuItem>
        </ContextMenu>
        <div
          onClick={this.loadPlaylist.bind(this, this.props._id)}
          className="sidebar-playlist"
        >
          {this.state.name}
        </div>
      </ContextMenuTrigger>
    );
  }
}
